<template>
  <div :class="className" :style="{ height: height, width: width }" />
</template>

<script>
import * as echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import resize from "./mixins/resize";

let now = new Date(1997, 9, 3);
// let oneDay = 24 * 3600 * 1000;
let value = Math.random() * 1000;

export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "550px",
    },
    autoResize: {
      type: Boolean,
      default: true,
    },
    testData11: {
      type: Array,
      default: [
        ["2022-11-09 17:54:43", 11],
        ["2022-11-09 17:56:43", 13],
      ],
    },
    // oxygenData: String,
    // temprature: String
    // chartData: {
    //   type: Object,
    //   required: true,
    // },
  },
  data() {
    return {
      chart: null,
      testData: [],
    };
  },
  // watch: {
  //   chartData: {
  //     deep: true,
  //     handler(val) {
  //       this.setOptions(val);
  //     },
  //   },
  // },
  watch: {
    testData11: {
      handler(val) {
        this.chart.setOption({
          series: [
            {
              data: val,
            },
          ],
        });
      },
    },
  },
  mounted() {
    this.height = `${window.screen.height/2}px`;
    this.$nextTick(() => {
      this.initChart();
    });
    // this.initChart();
    // for (var i = 0; i < 10; i++) {
    //   this.testData.push(this.randomData());
    // }
    // this.createData();
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
    clearInterval(this.dataTimer);
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.setOptions();
    },
    setOptions() {
      this.chart.setOption({
        xAxis: {
          type: "time",
          //data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          boundaryGap: false,
          axisLabel: {
            align:'center',
            formatter: function (value, index) {
              // params = params[0];
              // var date = new Date(params.name);
              // return (
              //   date.getMinutes() +
              //   ' : ' +
              //   date.getSeconds()
              // );
              // 格式化成时分秒
              // console.log('index:', index)
              let date = new Date(value);
              // let nowHours = date.getHours();
              let nowMin = date.getMinutes().toString().padStart(2,'0');
              let nowSeconds = date.getSeconds().toString().padStart(2,'0');
              var texts = [nowMin,nowSeconds];
              return texts.join(':');
              // return nowSeconds.toString();
            },
          },
          splitLine: {
            show: false,
          },
        },
        grid: {
          left: 10,
          right: 10,
          bottom: 20,
          top: 30,
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          backgroundColor: "#00A1F1",
          formatter: function (params) {
            console.log('params:', params)
            // params = params[0];
            let xdate = params.value[params.encode.x[0]];
            console.log('xdate:', xdate)
            var date = new Date(xdate);
            console.log('date:', date)
            return (
              date.getFullYear() +
              "-" +
              (date.getMonth() + 1) +
              "-" +
              date.getDate() +
              " " +
              date.getHours() +
              ":" +
              date.getMinutes().toString().padStart(2, "0") +
              ":" +
              date.getSeconds().toString().padStart(2, "0") +
              " -> " +
              params.value[1]
            );
          },
          axisPointer: {
            // type: "cross",
            animation: false
          },
          padding: [5, 10],
        },
        yAxis: {
          type:"value",
          axisTick: {
            show: false,
          },
        },
        legend: {
          // data: ["expected", "actual"],
          data: ["expected"],
        },
        dataZoom: {
          type: "inside",
        },
        series: [
          {
            name: "expected",
            markLine: {
              //最大值和最小值
              data: [
                {
                  name: "阈值",
                  yAxis: 3,//阈值设置
                  label: {
                    show: "true",
                    position: "middle",
                    formatter: "溶氧红线：3",
                    color:'inherit',
                    fontSize: 20,
                    fontWeight:"bold"
                  },
                  lineStyle: {
                    normal: {
                      width: 2,
                      color: "#FF5D1D",
                    },
                  },
                },
              ],
            },
            itemStyle: {
              normal: {
                color: "#FF005A",
                lineStyle: {
                  color: "#FF005A",
                  width: 2,
                },
              },
            },
            smooth: true,
            type: "line",
            // data: expectedData,
            // data: this.testData,
            data: this.testData11,
            animationDuration: 2800,
            animationEasing: "cubicInOut",
          },
        ],
      });
    },
    createData() {
      let that = this;
      that.dataTimer = setInterval(() => {
        that.testData.shift();
        that.testData.push(that.randomData());
        // console.log('testdata:', that.testData)
        that.chart.setOption({
          series: [
            {
              data: that.testData,
            },
          ],
        });
      }, 1000);
    },

    randomData() {
      // now = new Date(+now + oneDay);
      now = new Date();
      value = value + Math.random() * 21 - 10;
      // console.log('value:', value)
      // this.oxygenData = value;
      this.$emit("temperChange", `${Math.round(value)} mg/L`)
      return {
        name: now.toString(),
        value: [
          [now.getFullYear(), now.getMonth(), now.getDate()].join("/") +' '+ now.getHours() + ':' +now.getMinutes().toString().padStart(2,'0') + ':' + now.getSeconds().toString().padStart(2,'0'),
          // [now.getHours(), now.getMinutes(), now.getSeconds()].join(":"),
          // now.toString(),
          Math.round(value),
        ],
      };
    },
  },
};
</script>
